@media screen and (max-width: 1279px) {
  .menu-burger {
    width: 56px;
    height: 36px;
    display: inline-block;
    position: relative;
  }

  .menu-burger:before, .menu-burger:after {
    content: "";
    background-color: var(--sec-text-color);
    width: 100%;
    height: 2px;
    transition: all .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
  }

  .menu-burger:before {
    top: 0;
  }

  .menu-burger:after {
    bottom: 0;
  }
}

