    display: none;
  }
}

@media screen and (max-width: 1279px) {
  .menu-burger span {
    background-color: var(--sec-text-color);
    width: 100%;
    height: 2px;
    transition: scale .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    top: 17px;
    left: 0;
  }
}

.menu-burger.active {
  width: 56px;
  height: 36px;
  display: inline-block;
  position: relative;
}

.menu-burger.active span {
  scale: 0;
}

.menu-burger.active:before {
  top: 17px;
  transform: rotate(45deg);
}

.menu-burger.active:after {
  bottom: 17px;
  transform: rotate(-45deg);
}

